import React, { Component } from 'react'
import Link from 'next/link'

export default class FooterContentLink extends Component {
    constructor(props) {
        super(props);
        this.state = {
            iconVisibility: "hidden"
        };
    }
    hoverHanlder = () => {
        this.setState({
            iconVisibility: "unset"
        })
    }
    
    render() {
        return (
            <div onMouseEnter={this.hoverHanlder} onMouseLeave={this.hideIcon}>
                <i className={this.props.icon} style={{visibility: this.state.iconVisibility}}></i>
                <span>
                    <Link href={this.props.href}>{this.props.text}</Link>
                </span>
            </div>
        )
    }
}
